<template>
  <div>
    <div class="flex flex-col">
      <div class=" flex">
        <div class=" flex" v-if="get_auth.group == 'administrador'">
          <vs-button @click="set_pagos()" class=" focus:outline-none">
            Mostrar Todos los Registros
          </vs-button>

          <vs-button
              @click="agregar_nuevos_pagos()"
              class=" focus:outline-none"
          >
            Agregar Nuevos Registros
          </vs-button>
        </div>

        <vs-button
            @click="
            item_selected = {};
            dialog = true;
          "
            danger
            class=" focus:outline-none"
        >
          Pagos Exonerados
        </vs-button>
      </div>

      <div
          v-if="!mostrar"
          class=" flex flex-col lg:flex-row justify-between p-3 shadow-lg"
      >
        <div>
          <p>Importar</p>
          <input class=" mt-3" type="file" accept=".txt" @change="read_txt"/>
        </div>

        <div v-if="lineas.length > 0">
          <h1 class=" font-medium text-3xl text-gray-300">
            Dia: {{ lineas[0].fecha }}
          </h1>
        </div>

        <div class=" mt-3 lg:mt-0">
          <vs-button square @click="subirPagos">
            <i class="fas fa-arrow-up mr-2"></i>
            Subir pagos
          </vs-button>
        </div>
      </div>
    </div>

    <list
        :dataList="lineas"
        :countExist="lineas_existentes.length"
        @edit="edit"
        :actualizar="actualizar"
        @actualizado="actualizar = false"
    />

    <add
        :dialog="dialog"
        @closedialog="close_dialog"
        :item_selected="item_selected"
        @actualizar="actualizar = true"
    />
  </div>
</template>

<script>
import axios from "@//plugins//axios.js";

const List = () => import("./components/List.vue");
const Add = () => import("./components/Add.vue");

export default {
  components: {List, Add},
  data() {
    return {
      lineas: [],
      lineas_existentes: [],
      mostrar: true,
      item_selected: {},
      dialog: false,
      actualizar: false,
      admisionData: {},
      is_extemporaneo: false,
    };
  },

  computed: {
    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },

    SedeText() {
      if (this.get_auth.group == "sede") return this.get_auth.username;
      return "";
    },
  },

  async mounted() {
    await this.set_pagos();
    this.getAdmision();
  },

  methods: {
    async getAdmision() {
      // aun no se sabe si esta bien
      const {data} = await axios.get("admision");
      this.admisionData = data[0];
      const {fechaExtemporaneo} = this.admisionData;
      const fechaActual = Date().now;
      if (fechaExtemporaneo == fechaActual) {
        this.is_extemporaneo = true;
      }
    },

    refresh_reactivo() {
      this.lineas = this.$store.getters["pago/get_pago"];
    },

    close_dialog() {
      this.dialog = false;
      this.item_selected = {};
    },

    edit(e) {
      this.item_selected = null;
      this.item_selected = e;
      this.dialog = true;
    },

    agregar_nuevos_pagos() {
      this.lineas = [];
      this.lineas_existentes = [];
      this.mostrar = false;
    },

    async set_pagos() {
      try {
        this.mostrar = true;
        this.lineas = [];
        this.lineas_existentes = [];
        await this.$store.dispatch("pago/set_pago", {sede: this.SedeText});
        this.refresh_reactivo();
      } catch (error) {
        //
      }
    },

    async read_txt(e) {
      const loading = this.$vs.loading({
        color: "success",
      });

      try {
        const fun = () => {
          return new Promise((resolve) => {
            let input = e.target;
            var reader = new FileReader();
            reader.onload = function () {
              let text = reader.result;
              resolve(text);
            };
            reader.readAsText(input.files[0]);
          });
        };

        await fun().then((res) => {
          this.lineas = [];

          let tipo_doc, num_doc, fecha, monto, concepto, nombre, num_operacion;
          tipo_doc = num_doc = fecha = monto = concepto = nombre = num_operacion = "";

          let lineas = res.split("\n");

          if (lineas.length <= 0) {
            this.$vs.notification({
              title: "Cargar Archivo de Texto",
              text: "El txt no tiene datos! 😢",
              color: "warning",
            });
            return;
          }

          

          lineas.forEach((element) => {
            concepto = element.slice(35, 43); //8
            const condicion = parseInt(element.slice(112, 113));
            num_doc = element.slice(47, 62); //15
            num_doc = num_doc.trim();
            num_doc = num_doc.slice(7, 15);

            if (parseInt(concepto) === 574 && condicion === 2) {
              console.log("lineas", concepto, condicion, num_doc);
              tipo_doc = element.slice(25, 27); //2
              fecha = element.slice(79, 87); //8
              //
              const anio = fecha.slice(0, 4);
              const mes = fecha.slice(4, 6);
              const dia = fecha.slice(6, 8);
              fecha = `${anio}-${mes}-${dia}`;
              //
              num_operacion = element.slice(18, 25); //8
              monto = element.slice(62, 77);
              let decimales = 0;
              decimales = monto.slice(13, 15);
              monto = monto.slice(1, 13);
              monto = parseFloat(`${monto}.${decimales}`);
              nombre = element.slice(121, 156); //35
              //
              nombre = nombre.trim();
              nombre = nombre.split(" ");
              let aux = nombre.filter((item) => {
                if (item.length > 0) return item;
              });

              nombre = "";
              aux.forEach((element) => {
                nombre += element.trim() + " ";
              });

              nombre = nombre.trim();

              this.lineas.push({
                tipo_doc,
                num_doc,
                fecha,
                monto,
                nombre,
                num_operacion,
                repetido: false,
                is_exonerado: false,
                documento_referencial: "",
                monto_full: monto,
              });

              //
            }
          });
        });
      } catch (error) {
        //
      } finally {
        loading.close();
        this.lineas_existentes = [];
      }
    },

    async subirPagos() {
      if (this.lineas.length <= 0) {
        this.$vs.notification({
          title: "Subir al servidor",
          text: "No existen datos! 😢",
          color: "danger",
        });
        return;
      }

      const loading = this.$vs.loading({
        color: "success",
      });

      let data_envio = [];

      this.lineas.forEach((element) => {
        const is_extemporaneo =
            element.fecha == this.admisionData.fechaExtemporaneo;
        let obj = {
          descripcion: "ADMISION UNAP",
          monto: element.monto,
          num_ticket: element.num_operacion,
          fecha: element.fecha,
          dni: element.num_doc,
          nombre: element.nombre,
          sede: this.get_auth.group,
          monto_full: element.monto_full,
          is_extemporaneo: is_extemporaneo,
        };

        data_envio.push(obj);
      });

      try {
        const {data} = await axios.post("post_pagos_banko/", data_envio);

        this.lineas_existentes = data.data_existente;
        this.lineas.forEach((element, index) => {
          let indice = this.lineas_existentes.findIndex(
              (item) => item.num_ticket == element.num_operacion
          );
          if (indice > -1) {
            this.lineas[index].repetido = true;
          }
        });

        if (data.length <= 0) {
          this.$vs.notification({
            title: "Subir al servidor",
            text: "No se pudo subir los datos! 😢",
            color: "danger",
          });
          return;
        }

        this.$vs.notification({
          title: "Subir al servidor",
          text: "Los datos se subieron correctamente! 😎",
          color: "success",
        });
      } catch (error) {
        this.$vs.notification({
          title: "Subir al servidor",
          text: "Ocurrió un problema al subir los datos! 😢",
          color: "danger",
        });
      } finally {
        loading.close();
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
